// components/dynamic-zone/services/index.tsx
'use client';

import React from 'react';
import Link from 'next/link';

import { Heading } from '@/components/elements/heading';
import { Subheading } from '@/components/elements/subheading';
import { Button } from '@/components/elements/button';

type DZItem = {
  __component: string;
  id: number;
  heading?: string | null;
};

type PageLike = {
  id: number;
  documentId?: string;
  slug: string;
  dynamic_zone?: DZItem[];
};

type ButtonTarget = '_self' | '_blank' | '_parent' | '_top' | string;

type ButtonItem = {
  id?: number;
  documentId?: string;
  text?: string | null;
  URL?: string | null;
  href?: string | null;
  variant?: string | null;
  target?: ButtonTarget | null;
  newTab?: boolean | null;
};

type CTAComponent = {
  id?: number;
  documentId?: string;
  heading?: string | null;
  sub_heading?: string | null;
  CTAs?: ButtonItem[] | null; // belső lista
};

function getPageTitle(p: PageLike) {
  const hero = p.dynamic_zone?.find((x) => x.__component === 'dynamic-zone.hero');
  const t = hero?.heading?.trim();
  return t && t.length > 0 ? t : p.slug;
}

function toHref(btn: ButtonItem) {
  return (btn.URL ?? btn.href ?? '#') as string;
}

function toTarget(btn: ButtonItem) {
  const target = btn.newTab ? '_blank' : (btn.target ?? undefined);
  const rel = target === '_blank' ? 'noopener noreferrer' : undefined;
  return { target, rel };
}

export const Services = ({
  heading,
  sub_heading,
  pages,
  CTAs, // Strapi-ban ez egy komponens (CTA), benne CTAs[] gombokkal
}: {
  heading?: string | null;
  sub_heading?: string | null;
  pages?: PageLike[] | null;
  CTAs?: CTAComponent | null;
}) => {
  const pageItems = pages ?? [];
  const ctaSection = CTAs ?? null;
  const buttons = ctaSection?.CTAs ?? [];

  return (
    <section className="relative">
      <div className="pb-20">
        {!!heading && <Heading className="pt-4 text-center">{heading}</Heading>}
        {!!sub_heading && <Subheading className="text-center">{sub_heading}</Subheading>}

        {/* Pages (Services linkek) */}
        {!!pageItems.length && (
          <div className="mx-auto mt-10 grid max-w-5xl grid-cols-1 gap-4 px-4 sm:grid-cols-2 lg:grid-cols-3">
            {pageItems.map((p) => (
              <Link
                key={p.documentId ?? p.id}
                href={`/${p.slug}`}
                className="group rounded-2xl border border-white/10 bg-white/5 p-5 backdrop-blur transition hover:bg-white/8"
              >
                <div className="text-base font-semibold">{getPageTitle(p)}</div>
                <div className="mt-1 text-sm opacity-70">{p.slug}</div>

                <div className="mt-4 inline-flex items-center gap-2 text-sm font-medium opacity-80 transition group-hover:opacity-100">
                  Megnézem
                  <span aria-hidden className="translate-x-0 transition group-hover:translate-x-1">
                    →
                  </span>
                </div>
              </Link>
            ))}
          </div>
        )}

        {/* CTA blokk (külön komponens a Services-ben) */}
        {(!!ctaSection?.heading || !!ctaSection?.sub_heading || !!buttons.length) && (
          <div className="mx-auto mt-12 max-w-4xl px-4">
            {!!ctaSection?.heading && <Heading className="text-center">{ctaSection.heading}</Heading>}
            {!!ctaSection?.sub_heading && (
              <Subheading className="text-center">{ctaSection.sub_heading}</Subheading>
            )}

            {!!buttons.length && (
              <div className="mt-6 flex flex-wrap justify-center gap-3">
                {buttons.map((cta, idx) => {
                  const href = toHref(cta);
                  const { target, rel } = toTarget(cta);

                  return (
                    <Button
                      key={cta.documentId ?? cta.id ?? idx}
                      as="a"
                      href={href}
                      target={target}
                      rel={rel}
                      variant={(cta.variant as any) ?? 'primary'}
                    >
                      {cta.text ?? 'Tovább'}
                    </Button>
                  );
                })}
              </div>
            )}
          </div>
        )}
      </div>
    </section>
  );
};
